import { isAndroid, isIOS } from "react-device-detect";

import PropTypes from 'prop-types';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';

import axios from 'axios';

import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Container from "@material-ui/core/Container";


const useStyles = makeStyles(() => ({
  extendedIcon: {
    margin: 0,
    top: 'auto',
    right: 20,
    bottom: 70,
    left: 'auto',
    position: 'fixed',
  },
  root: {
    flexGrow: 1,
    position: "fixed",
    top: 0,
    width: "100%",
  },
}));

const url_from_device = () => {
  if (isAndroid) {
    return "intent://docs.google.com/document/d/1RTqNDl6ZnNMOAc_n_MLn0Z9Kiwvc7UaWHbpjwoRSWJg/edit#Intent;scheme=https;package=com.google.android.apps.docs.editors.docs;S.browser_fallback_url=market://details?id=com.google.android.apps.docs.editors.docs;end";
  } else if (isIOS) {
    return "googledocs://docs.google.com/document/d/1RTqNDl6ZnNMOAc_n_MLn0Z9Kiwvc7UaWHbpjwoRSWJg/edit";
  } else {
    return "https://docs.google.com/document/d/1RTqNDl6ZnNMOAc_n_MLn0Z9Kiwvc7UaWHbpjwoRSWJg/edit"
  }
};

const MyCard = (props) => {
  const data = props.data;
  return (
    <Card>
      <CardContent>
        <Typography variant="h5" component="h3">
          {data.name}
        </Typography>
      </CardContent>
    </Card>
  );
}
MyCard.propTypes = {
  data: PropTypes.object.isRequired
};

const filterd_after14days = (arr) => {
  return arr.filter(item => {
    const date = new Date(item["date"]);
    date.setDate(date.getDate() + 14);
    const today = new Date();
    return date < today;
  });
};

const UniqByLatest = (arr) => {
  const knownElements = new Map();
  for (const elem of arr) {
    const key = elem["name"];
    if (knownElements.has(key)) {
      const latest = knownElements.get(key);
      // comp latest, elem
      const day_map = new Date(latest["date"]);
      const day_elem = new Date(elem["date"]);
      if (day_elem > day_map) {
        knownElements.set(key, elem);
      }
    } else {
      knownElements.set(key, elem);
    }
  }
  const res = [];
  for (let value of knownElements.values()) {
    res.push(value);
  }
  return res;
}
const flatten_and_add_category = (users) => {
  const arr = [];
  users.forEach(item => {
    if (item.main) {
      arr.push({ 'category': 'main', 'name': item.main, 'date': item.date });
    }
    if (item.sub1) {
      arr.push({ 'category': 'sub', 'name': item.sub1, 'date': item.date });
    }
    if (item.sub2) {
      arr.push({ 'category': 'sub', 'name': item.sub2, 'date': item.date });
    }
    if (item.sub3) {
      arr.push({ 'category': 'sub', 'name': item.sub3, 'date': item.date });
    }
  });
  return arr;
};

const hoge = (data, cond) => {
  const category = cond["category"];
  if (category === "main") {
    return data.filter(item => item['category'] === category);
  } else if (category === "sub") {
    const subs = data.filter(item => item['category'] === category);
    return subs.filter(item => {
      let ok = true;
      for (let word of ["スープ", "汁", "サラダ"]) {
        if (item["name"].includes(word)) {
          ok = false;
        }
      }
      return ok;
    });
  } else {
    const words = cond["word"];
    return data.filter(item => {
      for (let word of words) {
        if (item["name"].includes(word)) {
          return true;
        }
      }
      return false;
    });
  }
}
const randgen = () => {
  return Math.floor(Math.random() * Math.floor(365));
}

const shuffled_arr = (arr) => {
  const shuffle_t = 1000;
  for (let t = 0; t < shuffle_t; ++t) {
    const i = randgen() % arr.length;
    const j = randgen() % arr.length;
    [arr[i], arr[j]] = [arr[j], arr[i]];
  }
  return arr;
}

const AllList = (props) => {
  let [menus, setMenus] = useState([]);
  useEffect(async () => {
    const response = await axios("https://raw.githubusercontent.com/Yang-33/MyGitHubPages/master/Gohan/homemenulist.json",);
    //const response = await axios("http://localhost:3000/Gohan/homemenulist.json",);
    setMenus(response.data);
  }, []);

  let filtered_menus = [];
  if (menus.length) {
    const datas = flatten_and_add_category(menus);
    const latest_datas = UniqByLatest(datas);
    filtered_menus = filterd_after14days(latest_datas);
    filtered_menus = hoge(filtered_menus, props.value);
    filtered_menus = shuffled_arr(filtered_menus);
  }

  /// ddd に整形したデータが入っているので、なんとかする
  const AllListstyle = {
    marginTop: "40px"
  };


  const cards = (
    <Grid container spacing={2} style={AllListstyle}>
      {filtered_menus.map((item, idx) => {
        return (
          <Grid item xs={6} key={idx}>
            <MyCard data={item} key={idx} />
          </Grid>
        );
      })}
    </Grid>
  );
  return (
    cards
  );
};

const CenteredTabs = (props) => {
  const classes = useStyles();

  return (
    <Paper className={classes.root}>
      <Tabs
        value={props.value}
        onChange={props.handleChange}
        indicatorColor="primary"
        textColor="primary"
        centered
      >
        <Tab label="メイン" />
        <Tab label="サラダ" />
        <Tab label="スープ" />
        <Tab label="1品" />
      </Tabs>
    </Paper>
  );
}
CenteredTabs.propTypes = {
  value: PropTypes.number,
  handleChange: PropTypes.func
};

const SearchableList = () => {
  const [value, setValue] = useState(0);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const f = (value) => {
    if (value == 0) {
      return { "category": "main", "word": [] };
    } else if (value == 1) {
      return { "category": "", "word": ['サラダ'] };
    } else if (value == 2) {
      return { "category": "", "word": ['スープ', '汁'] };
    } else if (value == 3) {
      return { "category": "sub", "word": [] };
    }
  }
  return (
    <div>
      <CenteredTabs value={value} handleChange={handleChange} />
      <AllList value={f(value)}/>
    </div>
  );
}

const Godocs = () => {
  const classes = useStyles();
  const DOC_URL = url_from_device();

  return (
    <Container>
      <div className={classes.extendedIcon}>
        <Fab color="primary" variant="extended" href={DOC_URL} target="_blank" rel="noopener noreferrer">
          <DescriptionOutlinedIcon />
          Open Docs
        </Fab>
      </div>
      <SearchableList />
    </Container>
  )
}

export default Godocs;
